:root {
  --primary: #2c3e50;
  --secondary: #3498db;
  --accent: #e74c3c;
  --light: #ecf0f1;
}

.container {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  gap: 20px;
}

.leftSection,
.rightSection {
  flex: 1;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.leftSection h2,
.rightSection h2 {
  color: #333;
}

.selectBox,
.inputField {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.calculateButton,
.downloadButton {
  width: 100%;
  padding: 12px;
  margin-top: 20px;
  background-color: #ff6600;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

.calculateButton:hover,
.downloadButton:hover {
  background-color: #e05500;
}

.rightSection h3 {
  font-size: 2rem;
  color: #007bff;
}

.resultItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  padding: 10px 0;
  border-top: 1px solid #ddd;
}

.resultItem span {
  font-size: 1rem;
}

.resultItem span:first-child {
  flex: 1;
}

.resultItem span:nth-child(2) {
  color: #28a745;
  font-weight: bold;
}

:root {
  --primary: #2c3e50;
  --secondary: #3498db;
  --accent: #e74c3c;
  --light: #ecf0f1;
  --gray: #95a5a6;
}
body {
  font-family: "Arial", sans-serif;
  background: var(--light);
  margin: 0;
  padding: 20px;
}
.calculator_container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
h1,
h2 {
  color: var(--primary);
  text-align: center;
  margin-bottom: 30px;
}
.calculator_tabs {
  display: flex;
  margin-bottom: 30px;
  border-bottom: 2px solid var(--light);
}
.tab {
  padding: 15px 30px;
  cursor: pointer;
  background: transparent;
  border: none;
  border-bottom: 3px solid transparent;
  color: var(--gray);
  font-weight: bold;
  transition: all 0.3s ease;
}
.tab.active {
  color: var(--secondary);
  border-bottom: 3px solid var(--secondary);
}
.input_group {
  margin-bottom: 20px;
}
label {
  display: block;
  margin-bottom: 5px;
  color: var(--primary);
  font-weight: bold;
}
select,
input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 15px;
}
input:focus,
select:focus {
  outline: 2px solid var(--secondary);
  border-color: var(--secondary);
}
button {
  background: var(--secondary);
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  font-size: 16px;
  transition: all 0.3s ease;
}
button:hover {
  background: #2980b9;
  transform: translateY(-2px);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.results {
  margin-top: 30px;
  padding: 20px;
  background: var(--light);
  border-radius: 5px;
  display: none;
}
.chartContainer {
  margin-top: 20px;
  position: relative;
  width: 100%;
  height: 400px;
}
.question {
  background: #f8f9fa;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  border-left: 4px solid var(--secondary);
}
.assumption-box {
  background: #f8f9fa;
  padding: 20px;
  margin: 20px 0;
  border-radius: 5px;
}
.assumption-title {
  font-weight: bold;
  color: var(--primary);
  margin-bottom: 15px;
}
.slider-container {
  margin: 15px 0;
}
.slider-container input[type="range"] {
  width: 100%;
  margin: 10px 0;
}
.slider-values {
  display: flex;
  justify-content: space-between;
  color: var(--gray);
  font-size: 0.9em;
}
.summary-metrics {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 30px;
}
.metric-card {
  background: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
}
.metric-value {
  font-size: 24px;
  font-weight: bold;
  color: var(--secondary);
  margin: 10px 0;
}
.sensitivity-analysis {
  margin-top: 30px;
}
.toggle-details {
  background: none;
  border: none;
  color: var(--secondary);
  cursor: pointer;
  padding: 10px;
  font-size: 14px;
}

